body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --casa-dimention: 70px;
}

.jogo {
  margin: 30px;
}

.controles {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}

.tabuleiro {
  background: #e7b73e;
  height: 80%;
  padding: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.casa-tabuleiro {
  background: antiquewhite;
  width: 70px;
  width: var(--casa-dimention);
  height: 70px;
  height: var(--casa-dimention);
  line-height: 70px;
  line-height: var(--casa-dimention);
  text-align: center;
  margin: 20px;
  font-family: 'Dokdo', cursive;
  font-size: 50px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.label-casa {
  position: absolute;
  left: 20px;
}

.pino {
  width: 50px;
  height: 50px;
  z-index: 1;
  position: absolute;
  margin: -15px;
}
.casa-tabuleiro .pino:nth-child(2) {
  margin: -15px 0 0 45px;;
}
.casa-tabuleiro .pino:nth-child(3) {
  margin: 45px;
}
.casa-tabuleiro .pino:nth-child(4) {
  margin: 45px 0 0 -15px; 
}

.pino-arcoiro {
  background-image: url(/icons/arcoiro.svg);
}

.pino-abacalino {
  background-image: url(/icons/abacalino.svg);
}

.pino-etezudo {
  background-image: url(/icons/etezudo.svg);
}

.pino-fernanda {
  background-image: url(/icons/fernanda.svg);
}

.casa-P { color: limegreen; -webkit-transform: rotate(15deg); transform: rotate(15deg); }
.casa-O { color: mediumpurple; -webkit-transform: rotate(-5deg); transform: rotate(-5deg); }
.casa-A { color: orange; -webkit-transform: rotate(10deg); transform: rotate(10deg); }
.casa-D { color: red; -webkit-transform: rotate(0); transform: rotate(0); }
.casa-L { color: blue; -webkit-transform: rotate(-20deg); transform: rotate(-20deg); }
.casa-T { color: gray; -webkit-transform: rotate(15deg); transform: rotate(15deg); }

.dice-btn {
  background-image: url(/icons/dice.png);
  width: 32px;
  height: 32px;
  border: 0;
  cursor: pointer;
}

.resultadoDado {
  font-family: 'Dokdo', cursive;
  font-size: 32px;
  margin: -3px 0 0 10px;
}
